.header,
.menu {
	display: flex;
	justify-content: space-between;
	gap: 1em;
}

.header {
	background-color: rgba(189, 132, 10, 0.5);
	color: white;
	padding: 1em 2em;
	font-size: 1.5rem;
}

.centered {
	width: 70%;
	margin: 0 auto;
}

button {
	padding: 0.3em 1em;
	font-size: 1.5em;
	background-color: rgb(216, 95, 25);
	border: none;
	margin: 1em 0 1em 0;
	cursor: pointer;
}

button:hover {
	color: rgb(216, 95, 25);
	background-color: black;
}

.joke {
	background-color: rgba(218, 69, 69, 0.2);
	padding: 1em;
	margin-bottom: 1em;
	border-radius: 10px;
}

.content {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.content div + div {
	margin-top: 2em;
}

form {
	background-color: rgba(0, 0, 0, 0.5);
	padding: 2em;
}

form > input[type="text"],
form > textarea {
	font-size: 1.4rem;
	padding: 0.2em;
	border-radius: 10px;
}

form > input[type="text"]:focus,
form > textarea:focus {
	border: none;
	background: chocolate;
}

form > * {
	display: block;
	width: 50%;
	margin-left: auto;
	margin-right: auto;
}
